import api from '../BaseAuth';

export default (guid, tipo, resourceKey, config, token) => {

  return api.post('/getbot/set-resources',{

      "guid": guid,
      "tipo":tipo,
      "resourceKey":resourceKey,
      "config":config,
},{
  headers: {
    'Content-Type': 'application/json',
    'x-access-token': token
  }})
}