import React, { useState, useEffect } from 'react';

function WordCloud({ info }) {

  const [URL, setURL] = useState('');
  const [key, setKey] = useState(info);


  const fn = () => {
    setKey(info);
  }

  useEffect(() => {
    fn()
    setURL(`https://wilkor.github.io/Nuvem-palavras/nuvem-de-palavras.html?key=${key}`);
  }, [fn])


  return (
    <>

      <iframe src={URL} width='100%' height='900' frameBorder="0"></iframe>


    </>

  );
}


export default WordCloud;