import React, { useState, useEffect } from 'react';
import handleSetResource from "../../services/handleSetResource";
import guid from '../../utils/guid';
import { BlipLoading } from '../BlipLoading';
import { NotificationContainer } from 'react-notifications';
import { showToast } from "../../api/commomService";

import handleGetResourceAll from "../../services/handleGetResourceAll";
import handleGetResourceItems from "../../services/handleGetResourceItems";



import TextPreview from '../BlipTextPreview/index';


function ConfigResource({ chave }) {


  const [chaveRecurso, setChaveRecurso] = useState('');
  const [targetState, setTargetState] = useState('');
  const [menu, setMenu] = useState(false);
  const [valida, setValida] = useState(true);
  const [validaHybrid, setValidaHybrid] = useState(true);

  const [loading, setLoading] = useState(false);
  const [mensager, setMensager] = useState('Some Text');
  const [resourceAll, setResourceAll] = useState([]);
  const [supportTeam, setSupportTeam] = useState('')



  const getResourceAll = async () => {

    const res = await handleGetResourceAll(guid(), chave);

    const menu = [];

    if (res.data.resource.items.length > 0) {

      for (let index = 0; index < res.data.resource.items.length; index++) {

        const response = await (await handleGetResourceItems(guid(), chave, res.data.resource.items[index])).data

        if (response.resource.config) {
          menu.push(res.data.resource.items[index]);
        }

      }

      setResourceAll(menu)



    }
  }


  useEffect(() => {
    getResourceAll()
  }, [])


  const handleRecordResource = async () => {

    setLoading(true);
    const getKey = chave;
    const config = {
      config: {
        valida,
        hybrid: validaHybrid,
        time: supportTeam,
        nextStep: {
          estadoDestino: targetState,
          menu
        }
      }
    }



    const result = await handleSetResource(guid(), getKey, "application/json", chaveRecurso, config);


    if (menu && validaHybrid) {

      const answer = `${chaveRecurso}_resposta`;
      await handleSetResource(guid(), getKey, "text/plain", answer, mensager);

    }


    if (result.data.status === 'success') {
      showMessage('Resource successfully registered!', 'success', 'Done!');

      return false;
    } else {
      showMessage('Error registering a resource!', 'danger', 'Error!');

      return false;
    }
  };


  function showMessage(msg, type, status) {

    setTimeout(() => {

      showToast({
        type: type,
        message: msg
      })
      setLoading(false);
    }, 900);

  }


  const handleTargetState = (e) => {

    const { value } = e.target;
    setTargetState(value);


  }

  const handleSupportTeam = (e) => {

    const { value } = e.target;
    setSupportTeam(value);


  }
  const handleMenu = (e) => {

    const { value } = e.target;
    const isTrueSet = (value === 'true');
    setMenu(isTrueSet);

  }

  const handleValida = (e) => {

    const { value } = e.target;
    const isTrueSet = (value === 'true');
    setValida(isTrueSet);

  }

  const handleValidaHybrid = (e) => {

    const { value } = e.target;
    const isTrueSet = (value === 'true');
    setValidaHybrid(isTrueSet);

  }


  const handleResourceKey = (e) => {

    console.log(e)
    const { value } = e.target;
    setChaveRecurso(value)
  }
  const handleMensager = (e) => {
    const { value } = e.target;
    setMensager(value);
  }

  const handleResourceChange = async (e) => {
    const { value } = e.target;

    if (value !== 'select') {

      const resultResourceItem = await (await handleGetResourceItems(guid(), chave, value)).data.resource.config

      if (menu && validaHybrid) {
        const resultResourceItemAnswer = await (await handleGetResourceItems(guid(), chave, `${value}_resposta`)).data.resource
        setMensager(resultResourceItemAnswer);
      }
      setChaveRecurso(value);
      setTargetState(resultResourceItem.nextStep.estadoDestino);
      setMenu(resultResourceItem.nextStep.menu);
      setValida(resultResourceItem.valida);
      setSupportTeam(resultResourceItem.time)
      setValidaHybrid(resultResourceItem.hybrid)
      return false
    }

    setMensager('Some Text');
    setChaveRecurso('');
    setTargetState('');
    setMenu(false);
    setValida(false);
  }


  return (
    <div className="container w-100">

      <h1>Configuration Registration</h1>
      <label>Resource Key: </label>
      <div className="row mb-3">

        <div className="row">
          <div className="col-sm">
            <div className="col-9">
              <input
                type="text"
                name="chave"
                className="bp-input-wrapper blip-input small "
                placeholder="Enter Resource Key"
                value={chaveRecurso}
                onChange={e => handleResourceKey(e)}
              />
            </div>    </div>
          <div className="col-sm">

            <div className="col-sm">
              <select name="valida" onChange={e => handleResourceChange(e)} className="bp-input-wrapper blip-select small">
                <option value="select">Select base Config</option>
                {resourceAll.map((e, idx) => {
                  return (<option value={e} key={idx}>{e}</option>)
                })}
              </select>
            </div>

          </div>

        </div>



      </div>

      <div className="mt-2 mb-2">
        <div className="row">

          <div className="col-3">
            <label>Destination State:</label>
            <input
              type="text"
              className="bp-input-wrapper blip-input small "
              name="estadoDestino"
              placeholder="Enter the Destination state"
              value={targetState}
              onChange={e => handleTargetState(e)}
            />
          </div>
          <div className="col">
            <label>Menu Type:</label>
            <select name="menu" value={menu} className="bp-input-wrapper blip-select small" onChange={e => handleMenu(e)}>
              <option value="select">Select</option>
              <option value="true">true</option>
              <option value="false">false</option>
            </select>
          </div>
          <div className="col">
            <label>Validate answer:</label>
            <select name="valida" value={valida} className="bp-input-wrapper blip-select small" onChange={e => handleValida(e)}>
              <option value="select">Select</option>
              <option value="true">true</option>
              <option value="false">false</option>
            </select>
          </div>

          {menu && (
            <div className="col">
              <label>hybrid:</label>
              <select name="validaHybrid" value={validaHybrid} className="bp-input-wrapper blip-select small" onChange={e => handleValidaHybrid(e)}>
                <option value="select">Select</option>
                <option value="true">true</option>
                <option value="false">false</option>
              </select>
            </div>
          )}

          <div className="col-3">
            <label>Enter a support team:</label>
            <input
              type="text"
              className="bp-input-wrapper blip-input small "
              name="team"
              placeholder="Enter a support team"
              value={supportTeam}
              onChange={e => handleSupportTeam(e)}
            />
          </div>
        </div>
        <hr />
      </div>

      {menu && validaHybrid ? (

        <div className="mt-2 mb-2">
          <div className="row">
            <div className="col-12">
              <label>Message:</label>
            </div>
            <div className="col-12">
              <textarea
                className="bp-textarea-wrapper blip-textarea w-100"
                rows="5"
                name="estadoDestino"
                placeholder="Enter your message"
                value={mensager}
                onChange={e => handleMensager(e)}

              />
            </div>
          </div>
          <hr />
          <TextPreview data={mensager} />

        </div>

      ) : ''}

      <div className="">
        <button onClick={handleRecordResource} className="bp-btn bp-btn--bot bp-btn--small">
          {loading && (
            <BlipLoading />
          )}
          {loading && <span>Save</span>}
          {!loading && <span>Save</span>}
        </button>
      </div>
      <NotificationContainer />
    </div>
  );

}


export default ConfigResource;